import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  ScaleControl,
} from 'react-leaflet'
import { Card, Rating } from 'semantic-ui-react'
import { getCountryFeature } from '../reducers/countryDataReducer'
import tileLayer from '../util/tileLayer'

//

const MapWrapper = ({ lt, lg, zm, features }) => {
  const dispatch = useDispatch()
  const state = useSelector((state) => state)
  const details = useSelector((state) => state.country.featuresDetails)
  console.log('details', details)
  const center = [lt, lg]

  useEffect(() => {
    features.map((f) => dispatch(getCountryFeature(f.properties.place_id)))
  }, [features])

  console.log('state', state)

  return (
    <MapContainer
      //  style={{ width: '100%', height: '55vh' }}
      id="map"
      center={center}
      zoom={zm}
    >
      <TileLayer {...tileLayer} />

      {details.map((feature) => {
        const loc = feature[0].properties
        //const wikidata = {`https://www.wikidata.org/wiki/${loc.wiki_and_media?.wikidata}`}
        const lat = loc.lat
        const lng = loc.lon
        const title = loc.name
        const id = loc.place_id
        const address0 = `${loc.housenumber}`
        const address1 = `${loc.street}`
        const address2 = `${loc.city}, ${loc.state} ${loc.postcode}`
        const website = loc.website
        const phone = loc.contact?.phone
        const stars = loc.accommodation?.stars
        return (
          <>
            <React.Fragment key={id}>
              <Marker position={{ lat, lng }}>
                <Popup>
                  <Card>
                    <Card.Content>
                      {stars && stars > 0 ? (
                        <Rating
                          icon="star"
                          defaultRating={stars}
                          maxRating={5}
                          disabled
                        />
                      ) : null}
                      <Card.Header>{title} </Card.Header>
                      {phone && <Card.Meta> {phone} </Card.Meta>}
                      <Card.Description>
                        {address0 !== 'undefined' ? address0 : ''}{' '}
                        {address1 && address1}
                      </Card.Description>
                      <Card.Description>
                        {address2 && address2}
                      </Card.Description>
                    </Card.Content>
                    <Card.Content extra as="a" href={website}>
                      {website}{' '}
                    </Card.Content>
                  </Card>
                </Popup>
              </Marker>
            </React.Fragment>
          </>
        )
      })}
      <ScaleControl imperial={false} />
    </MapContainer>
  )
}

export default MapWrapper
